import React, { useState } from 'react';
import { Layout, Typography, Form, Radio, Button, message } from 'antd';
import { SmileOutlined } from '@ant-design/icons';
import './PPT.css';
import { Tile } from './Tile';

const { Header, Content } = Layout;
const { Title, Paragraph } = Typography;

export const ProfessionalPsychologicalTest = () => {
  const [form] = Form.useForm();
  const [result, setResult] = useState(null);

  const onFinish = (values) => {
    const selectedOption = values['psychologicalTest'];
    let testResult = '';

    switch (selectedOption) {
      case 'stressIndex':
        testResult = '  : 85%';
        break;
      case 'selfAssessment':
        testResult = '    : 70%';
        break;
      case 'relationships':
        testResult = '  : 95%';
        break;
      default:
        testResult = ' ';
    }

    setResult(testResult);
    message.success(` : ${testResult}`);
  };

  return (
    <>
      <div className="form-container">
        <Form form={form} onFinish={onFinish}>
          <h2>AI  </h2>
          <h3>   </h3>
          <Form.Item
            name="psychologicalTest"
            rules={[
              {
                required: true,
                message: ' .',
              },
            ]}
          >
            <Radio.Group>
              <Radio value="stressIndex">  </Radio>
              <Radio value="selfAssessment">    </Radio>
              <Radio value="relationships">  </Radio>
            </Radio.Group>
          </Form.Item>
          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              icon={<SmileOutlined />}
              className="submit-button"
            >
                ( )
            </Button>
          </Form.Item>
        </Form>
        {result && (
          <div className="result">
            <h3 level={3} className="result-title">
               
            </h3>
            <Tile />
            {/* <Paragraph className="result-text">{result}</Paragraph> */}
          </div>
        )}
      </div>
    </>
  );
};